@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2rem;
	width: 100%;
	@include m.tablet {
		width: auto;
		flex-direction: row;
	}
	&__link {
		font-size: 2rem;
		font-weight: 700;
		&--registrar {
			@include m.boton(v.$primario, v.$blanco);
		}
	}
}
