@use "base/variables" as v;
@use "base/mixins" as m;

.header {
	display: flex;
	flex-direction: column;
	padding: v.$separacion 0;
	align-items: center;

	@include m.tablet {
		flex-direction: row;
		justify-content: space-between;
	}
	&__logo {
		width: 30rem;
		margin-bottom: v.$separacion;
		@include m.tablet {
			margin-bottom: 0;
		}
	}
}
